.slider {
  position: relative;
  margin: 60px 0 0 0; }

.slider__container {
  width: 1170px;
  min-height: 346px;
  overflow: hidden;
  margin: 0 auto; }

.slider__wrapper {
  width: 1170px;
  /*overflow: hidden;*/ }

.slider__items {
  display: flex;
  justify-content: space-between;
  transition: transform 0.5s ease; }

.slider_disable-transition {
  transition: none; }

.slider__item {
  display: flex;
  justify-content: space-between;
  flex: 0 0 100%;
  height: 100%;
  max-width: 100%;
  user-select: none;
  box-sizing: border-box; }

.slider__item-container {
  width: 270px;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .slider__item-container p {
    margin: 0;
    font-family: "Poppins", "Arial", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 21px;
    color: #1D1613; }
  .slider__item-container p:first-of-type {
    margin: 25px 0 0 0; }
  .slider__item-container p:last-of-type {
    margin: 15px 0 0 0;
    font-weight: 700;
    font-size: 14px; }

/* кнопки влево и вправо */
.slider__control {
  position: absolute;
  top: -95px;
  width: 29px;
  height: 9px;
  cursor: pointer; }

.slider__control_hide {
  display: none; }

.slider__control[data-slide='prev'] {
  left: calc(100vw/2 - 585px); }

.slider__control[data-slide='next'] {
  left: calc(100vw/2 + 550px); }

.slider__control:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 29px;
  height: 11px; }

.slider__control[data-slide='prev']::before {
  background-image: url("../../img/icon-arrow-right.svg"); }

.slider__control[data-slide='next']::before {
  background-image: url("../../img/icon-arrow-right.svg");
  transform: rotate(180deg); }

/* индикаторы */
.slider__indicators {
  width: 48px;
  line-height: 12px;
  margin: 60px auto 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  list-style: none; }

.slider__indicators li {
  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  color: #9B9997;
  cursor: pointer; }

.slider__indicators li.active {
  color: #1D1613; }

