.questions {
  width:970px;
  margin:130px auto 0;

  h2 {
    font-family: $cormorant;
    font-style: normal;
    font-weight: 700;
    font-size: 34px;
    line-height: 44px;
    text-align: center;
    letter-spacing: 0.03em;
    color: $text-dark;
    margin: 0 auto;
  }
}

.questions__list {
  width:100%;
  margin:60px auto 0;
  padding: 0;
  border:solid 1px $second;
  box-sizing: border-box;
  list-style-type: none;
}

.questions__list-element {
  position: relative;
  min-height:112px;
  box-sizing: border-box;
  padding:50px 0 49px 38px;
  border-bottom:solid 1px $second;

    h3 {
      max-width:527px;
      margin:0;
    }

    p {
      display: none;
      max-width:527px;
      font-family: $poppins;
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 21px;
      color: $text-dark;
      margin:20px 0 0 0;
    }

    svg {
      position: absolute;
      top:52px;
      right:50px;
      fill:none;
      stroke:$second;
      transform: rotate(180deg);
    }

    &.questions__list-element--open {

      p {
        display: flex;
      }

      svg {
        transform: rotate(0deg);
      }
    }
}
