.subscription {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  margin:0 88px 0 0 0;

  h2 {
    font-family: $cormorant;
    font-style: normal;
    font-weight: 700;
    font-size: 34px;
    line-height: 44px;
    letter-spacing: 0.03em;
    color: #FFFFFF;
    margin:0;
  }

  p {
    font-family: $poppins;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
    color: #9B9997;
    margin:25px 0 0 0;
  }
}

.subscription__form {
  display: flex;
  justify-content: space-between;
  min-width:500px;
  border-bottom:solid 1px #fff;
  padding:0 0 15px 0;
  margin:54px 0 0 0;

  input {
    min-width:250px;
    border:none;
    border-bottom:solid 1px transparent;
    padding:0 0 2px 0;
    background-color: transparent;
    box-sizing: border-box;
    font-family: $poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    color: #9B9997;
  }

  input:focus {
    outline: none;
    border: none;
    border-bottom:solid 1px #9B9997;
  }

  input::placeholder {
    font-family: $poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    color: #9B9997;
  }

  button {
    width:103px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: $poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 21px;
    color: #FFFFFF;
    border: none;
    outline:none;
    padding: 0;
    background-color: transparent;
    cursor: pointer;

    svg {
      stroke:#fff;
    }
  }

  button:hover {
    color:$second;

    svg {
      stroke:$second;
    }
  }
}
